*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --header-font: "Titan One", sans-serif;
  --main-font: "DM Sans", sans-serif;
  --coral-pink-headers: #ffa5ba;
  --dark-red-brand-color: #d41443;
  --bgc-products_first-color: #ffb8ca;
  --bgc-products_second-color: #f0d1a5;
  --bgc-products_third-color: #c2e297;
  --white-text-color: #fff;
  --light-coral-background-color: #fff5f6;
  --black-color: #000;
  --gray-color: #e1e1e1;
  --brown-color: #907e82;
}

body {
  font-family: var(--main-font);
}

.products-header-wrap {
  font-family: var(--header-font);
}

.products-first-header {
  color: var(--coral-pink-headers);
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1.2px;
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: cubic bezier(.4, 0, .2, 1);
  margin: 0;
  font-family: Titan One, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
  position: relative;
}

.products-first-header:hover, .products.first-header:focus {
  transform: scale(1.5);
}

.products-second-header {
  color: var(--dark-red-brand-color);
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 3.48px;
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: cubic bezier(.4, 0, .2, 1);
  margin: 0;
  font-family: Titan One, sans-serif;
  font-size: 34px;
  font-weight: 400;
  line-height: normal;
}

.products-second-header:hover, .products-second-header:focus {
  transform: scale(1.5);
}

.container {
  width: 100%;
  margin: auto;
  padding: 0 20px;
}

.products-section-container {
  padding: 60px 20px;
}

.products-card-list {
  flex-direction: column;
  align-items: center;
  gap: 0;
  list-style-type: none;
  display: flex;
}

.products-card {
  width: 280px;
  transition-property: transform, background-color, box-shadow;
  transition-duration: .25s;
  transition-timing-function: cubic bezier(.4, 0, .2, 1);
  min-height: 489px;
  border: none;
  border-radius: 24px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 80px;
  padding-left: 32px;
  padding-right: 32px;
  display: flex;
}

.products-card:hover {
  transform: scale(1.05);
  box-shadow: 0 1px 2px rgba(196, 56, 56, .208), 0 2px 4px rgba(0, 0, 0, .07), 0 4px 8px rgba(0, 0, 0, .07), 0 8px 16px rgba(0, 0, 0, .07), 0 16px 32px rgba(0, 0, 0, .07), 0 32px 64px rgba(0, 0, 0, .07);
}

.products-card-image {
  width: 280px;
  height: 280px;
  border: none;
  flex-shrink: 0;
}

.products-card-items-wrapper {
  border-radius: 24px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 32px;
  padding: 0;
  display: flex;
}

.products-card-header {
  color: var(--white-text-color);
  text-align: center;
  letter-spacing: .8px;
  text-transform: uppercase;
  font-family: Titan One, sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: normal;
}

.products-card-icon {
  fill: var(--white-text-color);
}

.products-card-paragraph {
  color: var(--white-text-color);
  text-align: center;
  letter-spacing: .56px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 176%;
}

.product-photos {
  justify-content: center;
  align-items: center;
  gap: 32px;
  display: flex;
}

.icecoffee {
  background-color: var(--bgc-products_second-color);
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic bezier(.4, 0, .2, 1);
}

.icecoffee:hover, .icecoffee:focus {
  background-color: rgba(203, 156, 91, .694);
}

.products-card-2:nth-child(2):before {
  content: "";
  z-index: 1;
  width: 280px;
  height: 280px;
  background-image: url("icecoffee.5cb3f4b6.jpg");
  background-size: 280px 280px;
  position: absolute;
  bottom: 350px;
  left: 50%;
  transform: translateX(-50%);
}

.icecream {
  background-color: var(--bgc-products_first-color);
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic bezier(.4, 0, .2, 1);
}

.icecream:hover, .icecream:focus {
  background-color: #e5a7b8;
}

.milkshake {
  background-color: var(--bgc-products_third-color);
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic bezier(.4, 0, .2, 1);
}

.milkshake:hover, .milkshake:focus {
  background-color: #b2ce8c;
}

.products-card-button {
  background-color: var(--white-text-color);
  min-width: 40px;
  min-height: 40px;
  transition-property: transform, background-color, border-radius;
  transition-duration: .25s;
  transition-timing-function: cubic bezier(.4, 0, .2, 1);
  border: none;
  border-radius: 50%;
}

.products-card:hover .products-card-button {
  background-color: var(--dark-red-brand-color);
  border-radius: 15px;
  transform: rotate(-90deg)scale(1.05);
}

.products-card-button-icon {
  fill: #d41443;
}

.products-card:hover .products-card-button-icon {
  fill: #fff;
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
  .products-header-wrap {
    margin-bottom: 20px;
  }

  .products-first-header {
